
import { defineFlow } from '@genkit-ai/flow';
import { z } from 'zod';
import { geminiPro } from 'genkitx-googleai';

export const summarizeWebsiteTerms = defineFlow(
  {
    name: 'summarizeWebsiteTerms',
    inputSchema: z.object({
      terms: z.string(),
    }),
    outputSchema: z.object({
      summary: z.string(),
    }),
  },
  async (input) => {
    const prompt = `Please summarize the following terms and conditions from a website. Focus on what personal data the website collects, how it's used, and if it's shared with third parties. Present the summary in a clear, concise, and easy-to-understand manner.

Terms and Conditions:
${input.terms}`;

    const llmResponse = await geminiPro.generate({
      prompt: prompt,
      config: {
        temperature: 0.3,
      },
    });

    return {
      summary: llmResponse.text(),
    };
  }
);
